ote-text{font-size:20px}.content-number-circle{background-color:black;color:#fff;border-radius:25px;padding:15px;font-weight:700;font-size:20px}.recognition-text{line-height:1.5;font-size:25px;margin:60px 0px}@media screen and (min-width: 1200px){.header-container{margin:0 200px;margin-top:200px}.header-container .header-title{font-size:40px}.wordsoup-wrapper .blog-content{padding:20px 200px;padding-bottom:40px}.thanks-wrapper{padding:0px 200px}.content-container{padding:80px 200px}.quote-wrapper{padding:80px 200px}}@media only screen and (min-width: 601px) and (max-width: 900px){.wordsoup-wrapper .wordsoup-first-text{padding-top:120px;font-size:32px}.wordsoup-wrapper .wordsoup-second-text{font-size:25px;padding-top:24px}}@media only screen and (max-width: 850px){.header-container{margin:0 80px;margin-top:200px}.wordsoup-wrapper .blog-content{padding:20px 80px;padding-bottom:40px}.thanks-wrapper{padding:0px 80px}.content-container{padding:40px 80px}.quote-wrapper{padding:40px 80px}}@media only screen and (max-width: 650px){.header-container{margin:0 80px;margin-top:80px}.wordsoup-wrapper .blog-content{padding:20px 50px;padding-bottom:40px}.thanks-wrapper{padding:0px 50px}.content-container{padding:40px 50px}.quote-wrapper{padding:40px 50px}}@media only screen and (max-width: 600px){.header-container .header-title{font-size:32px}.wordsoup-wrapper .wordsoup-first-text{padding-top:120px;font-size:20px}.wordsoup-wrapper .wordsoup-second-text{font-size:15px;padding-top:80px}.wordsoup-wrapper .blog-content{padding-bottom:40px}}

